import { Component, Injector, Input, OnInit } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { ConfirmationService, MenuItem, PrimeIcons, TreeNode } from 'primeng/api';
import { AlertService } from '../@core/service/alert.service';
// import { HttpService } from '../@core/service/http.service';
import { RbacService } from '../@core/service/rbac.service';
import { ShareDataService } from '../@core/service/share-data.service';
import { RBACINFO } from '../@core/urls/rbac-url.config';
import { Subscription } from 'rxjs';
import { DataStoreService } from '../@core/service/data-store.service';
import {AppConstants} from '../@core/constants/app-constants';
import { debounceTime, distinctUntilChanged, map } from 'rxjs/operators';
declare const $ : any;
@Component({
  selector: 'permissions',
  templateUrl: './permissions.component.html',
  styleUrls: ['./permissions.component.scss']
})
export class PermissionsComponent implements OnInit {
  pages!: TreeNode[];
  menuItems: MenuItem[] = [];
  filteredPermissionList: any[] = [];
  pageForm!: FormGroup;
  permissionForm!: FormGroup;
  permissions!: any[];
  permissionTypes!: any[];
  nodeType: string;
  saveMode: string;
  selectedItem: any = {};
  imageInformation: any;
  deletedId: number;
  urlPath: any;
  uploadedFile: any;
  attachType: any;
  imageData!: { contentType: any; fileName: any; };
  attachmentService: any;
  sanitizer: DomSanitizer;
  pageAccessService: any;
  pagesList: any;
  RBACORG: RBACINFO = new RBACINFO();
  orgSubs!: Subscription;
  orgId: any;
  position: string = 'top';
  showLinkPage!: boolean;
  environment: any;
  duplicatepages: any[] =[];
  httpService:any;
  modelPermissiomName: string = '';
  iconList: any;
  selectedIconLabel: any;
  dublicateIconList: any[] =[];
  treeData: any[] = [];
  search: FormControl;
  constructor(
    injector: Injector,
    private permissionService: RbacService,
    private formBuilder: FormBuilder,
    private alertService: AlertService,
    private confirmationService: ConfirmationService,
    // private httpService: HttpService,
    private _shareData: ShareDataService,
    private _storeservice: DataStoreService
  ) {
    this.nodeType = 'page';
    this.saveMode = 'INSERT';
    this.sanitizer = injector.get<DomSanitizer>(DomSanitizer);
    this.initializePageForm();
    this.initializePermissionForm();
    const iconArray = AppConstants.iconList;
    this.iconList = iconArray.sort((a, b) => a.label.localeCompare(b.label));
    this.dublicateIconList = this.iconList;
  }

  ngOnInit(): void {
    this.orgSubs =  this._storeservice.currentStore.subscribe((res: any) => {
      if (res['RBACORG'] && res['RBACORG'] !== '') {
        this.RBACORG = res['RBACORG'];
        console.log(this.RBACORG, 'RBACORG Permisson');
        this.environment = this.RBACORG['environment'];
        this.orgId = parseInt(this.RBACORG['orgID']);
        this.httpService = res['HTTPSERVICE']
        if (this.orgId) {
          this.setPagesList();
        }
        if(this.environment){
          this.loadInitial();
        }
      }
    });
    this.search = new FormControl();
    this.search.valueChanges
      .pipe(
        debounceTime(500),
        distinctUntilChanged(),
        map((value) => value?.toLowerCase())
      )
      .subscribe((value) => {
        const filtered = this.pages.filter((a: any) =>
          a?.name?.toLowerCase().startsWith(value)
        );
        const filteredIds = filtered.map((item: any) => item.id);
        const child = this.pages.filter((item: any) => filteredIds.includes(item.parentid));
        this.treeData = this.buildTree([...filtered, ...child], null);
      });
  }
  ngOnDestroy(): void {
    this.orgSubs.unsubscribe();
  }
  loadInitial(){
    this.loadTree();
    this.loadContextMenu();
    this.permissionService.getPermissionsTree(this.environment.applicationid).subscribe((items: any) => {
      this.permissions = items.data;
    });

    this.permissionService.getPermissionTypes(this.environment.applicationid).subscribe((items: any) => {
      this.permissionTypes = items.data;
    });
  }
  initializePageForm() {
    this.pageForm = this.formBuilder.group({
      id: [0],
      parentid: [null],
      applicationid: [this.environment ? this.environment['applicationid']: ''],
      name: ['', Validators.required ],
      route: ['', Validators.required],
      order: [0],
      ismenu: [false],
      thumbnail: [''],
      icon: ['',Validators.required]
    });
  }

  initializePermissionForm() {
    this.permissionForm = this.formBuilder.group({
      id: [0],
      parentid: [null],
      pageid: [0],
      permissiontypeid: ['', Validators.required],
      key: ['', Validators.required],
      description: ['', Validators.required],
      order: [0]
    });
  }

  buildTree(pages: any[], parentid: any): any[] {
    const tree: any[] = [];

    pages
      .filter(page => page.parentid === parentid)
      .forEach(page => {
        const children = this.buildTree(pages, page.id);

        if (children.length > 0) {
          page.children = children;
        } else {
          page.children = [];
        }

        tree.push(page);
      });

    return tree;
  }

  onNodeContextMenuSelect(_event: any) {
    console.log();
  }

  nodeSelect(event: any) {
    this.saveMode = 'UPDATE';
    this.showLinkPage=false;
    this.nodeType = event.node.type;
    if (event.node.type === 'permission') {
      this.permissionForm.reset();
      this.permissionForm.patchValue(event.node);
    } else {
      this.pageForm.reset();
      this.pageForm.patchValue(event.node);
      this.getSelectedLabel(event.node?.additionalinfo?.icon);
      this.pageForm.patchValue({
        icon: event.node.additionalinfo?.icon || ''
      });
      if(event.node.route.includes('/pages/dynamic-search/search')){
        this.showLinkPage=true;
        this.pageForm.patchValue({
          route:event.node.route.split('/')[4]
        })
      }
      this.imageInformation = event.node.additionalinfo?.thumbnail?.fileName?.split('/')[1] || '';
    }
  }

  onNodeExpandClick(event: any, node: any): void {
    if (node.menutype !== 'page') {
      // Handle the expansion logic here
      // For example, you might want to prevent expansion for certain conditions
      event.preventDefault();
    }
  }

  nodeExpand(event: any) {
    // const pageId = event.node.type === 'permission' ? event.node.pageid : event.node.data;
    // const parentId = event.node.type === 'permission' ? event.node.data : 0;
    // if (event.node && event.node.data) {
    //   this.permissionService.getPermissionTree(pageId, parentId).subscribe((nodes: any) => {
    //     event.node.children = (<any>nodes).data;
    //   });
    // }
  }
  
  searchIconList(event: Event) {
    const value = (event.target as HTMLInputElement).value.toLowerCase();
    this.iconList = this.dublicateIconList.filter((a: any) => a?.label?.toLowerCase().startsWith(value));
  }
  
  getSelectedLabel(val){
    this.iconList = this.dublicateIconList;
    console.log(val);
    const filteredIcon = this.iconList.filter((i) => i.value === val);
    console.log(filteredIcon);
    this.selectedIconLabel = filteredIcon[0].label;    
 }

  savePage() {
    const page = this.pageForm.value;
    page.applicationid =this.environment.applicationid;
    page.additionalinfo = {
      icon: page.icon
    };
    if (this.showLinkPage) {
      page.route = `/pages/dynamic-search/search/${page.route}`;
    }
    if (this.pageForm.valid) {
      page.order = page.order ? Number(page.order) : 1;
      if (this.saveMode === 'INSERT') {
        this.permissionService.createPage(page).subscribe((res: any) => {
          // if (this.showLinkPage) {
            page.id = res['data'];
            this.savePermission(page);
          // }
          this.alertService.success('Page created successfully.');
          this.loadTree();
        });
      } else {
        this.permissionService.updatePage(page).subscribe(() => {
          this.alertService.success('Page updated successfully.');
          this.loadTree();
        });
      }
    } else {
      this.alertService.error('Please Fill All Required Fields.');
    }
  }

  savePermission(page?: any) {
    if (page) {
      this.permissionForm.patchValue({
        description: page.name,
        key: page.name.toUpperCase().replaceAll(' ', '_'),
        pageid: page.id,
        order: 1,
        permissiontypeid: 1
      });
    }
    const permission = {
      ...this.permissionForm.value,
      order: +this.permissionForm.value.order
    };

    if (this.permissionForm.valid) {
      permission.order = permission.order ? Number(permission.order) : 1;
      delete permission.readonly;
      if (this.saveMode === 'INSERT') {
        this.permissionService.createPermission(permission).subscribe(() => {
          this.alertService.success('Permission created successfully.');
          this.loadTree();
        });
      } else {
        this.permissionService.updatePermission(permission).subscribe(() => {
          this.alertService.success('Permission updated successfully.');
          this.loadTree();
        });
      }
    } else {
      // this.alertService.error('Invalid permission data.');
    }
  }

  clearForm() {
    if (this.nodeType === 'permission') {
      this.initializePermissionForm();
    } else {
      this.initializePageForm();
    }
  }
  resetForm() {
    this.saveMode = '';
    this.imageInformation = '';
    this.pageForm.reset();
    this.clearForm();
  }
  removeThumbnail() {
    this.pageForm.controls['thumbnail'].reset();
    this.imageInformation = '';
  }

  // delete(event: Event) {
  //   event.stopPropagation();
  //   // this.deletedId = this.selectedItem.id;
  // }


  createPermissionForm() {
    this.initializePermissionForm();
    if (this.nodeType === 'page') {
      this.nodeType = 'permission';
      this.permissionForm.patchValue({
        pageid: this.selectedItem.id
      });
    } else {
      this.permissionForm.patchValue({
        pageid: this.selectedItem.pageid,
        parentid: this.selectedItem.id
      });
    }
  }

  private loadContextMenu() {
    const permission = {
      SETTINGS_PER_CREATE_PAGE: true,
      SETTINGS_PER_CREATE_PERMISSION: true,
      SETTINGS_PER_DELETE: true
    };
    this.menuItems = [
      {
        label: 'Create Page',
        icon: PrimeIcons.ARROW_UP_LEFT,
        visible: permission.SETTINGS_PER_CREATE_PAGE,
        badge: 'SETTINGS_PER_CREATE_PAGE',
        command: (_event: any) => {
          this.saveMode = 'INSERT';
          this.nodeType = 'page';
          this.showLinkPage = false;
          this.clearForm();
        }
      },
      {
        label: 'Link Page',
        icon: PrimeIcons.ARROW_UP_LEFT,
        visible: permission.SETTINGS_PER_CREATE_PAGE,
        command: (_event: any) => {
          this.saveMode = 'INSERT';
          this.nodeType = 'page';
          this.showLinkPage = true;
          this.clearForm();
        }
      }
      ,
      {
        label: 'Create Permission',
        icon: PrimeIcons.ARROW_DOWN_RIGHT,
        visible: permission.SETTINGS_PER_CREATE_PERMISSION,
        badge: 'SETTINGS_PER_CREATE_PERMISSION',
        command: _event => {
          this.saveMode = 'INSERT';
          this.nodeType = this.selectedItem.type;
          this.clearForm();
          this.createPermissionForm();
        }
      }
      ,
      {
        label: 'Delete',
        icon: PrimeIcons.TRASH,
        visible: permission.SETTINGS_PER_DELETE,
        badge: 'SETTINGS_PER_DELETE',
        command: event => {
          this.saveMode = 'DELETE';
          this.nodeType = this.selectedItem.type;

          // this.confirmationService.confirm({
          //   target: event.target as EventTarget,
          //   message: 'Are you sure that you want to delete?',
          //   icon: 'pi pi-exclamation-triangle',
          //   accept: () => {
          //     //confirm action
          $('#Deleteuser').modal('show');
          // this.deleteItem();
          //   },
          //   reject: () => {
          //     // This is intentional
          //   },
          // });
        }
      }
    ];
  }

  setPagesList() {
    this.permissionService.getOrganizationPage(this.orgId).subscribe((res: any) => {
      const data = res['data'];
      if (data && data?.length) {
        this.pagesList = data?.filter((a: any) => a.activeVersion?.gridconfig).map((x: any) => {
            x.activeVersion.pageid = x.activeVersion.pageid.toString();
            return {
              id: x.activeVersion.pageid,
              pagename: x.activeVersion.pagename,
              value: x.activeVersion.pagename
            };
          });
      }
    });
  }

  private loadTree() {
    this.permissionService.getAllPageTree(this.environment.applicationid).subscribe((items: any) => {
      this.pages = items;
      this.duplicatepages = items;
      if (this.pages.length) {
        this.selectedItem = this.pages[0];
      }
      this.treeData = this.buildTree(this.pages, null);
    });
  }
  cancel(){
    this.permissionForm.reset();
  }
  // delete() {
  //   // event.stopPropagation();
  //   // this.deletedId = id;
  //   $('#Deleteuser').modal('show');
  // }
  deleteItem() {
    this.saveMode = 'UPDATE';
    if (this.selectedItem.type === 'page') {
      // $('#Deleteuser').modal('show');
      this.permissionService.deletePage(this.selectedItem.id).subscribe((_item: any) => {
        this.cancel();
        $('#Deleteuser').modal('hide');
        this.alertService.success('Page deleted successfully.');
        this.loadTree();
      });
    } else {
      this.permissionService.deletePermission(this.selectedItem.id).subscribe((_item: any) => {
        // $('#Deleteuser').modal('show');
        this.cancel();
        $('#Deleteuser').modal('hide');
        this.alertService.success('Control Permission deleted successfully.');
        this.loadTree();
      });
    }
  }

  requiredIfValidator(predicate: () => any) {
    return (formControl: AbstractControl) => {
      if (!formControl.parent) {
        return null;
      }
      if (predicate()) {
        return Validators.required(formControl);
      }
      return null;
    };
  }

  handleFileInput(fileValue: any) {
    const target = fileValue.target as HTMLInputElement;
    const file: File = target.files![0];
    this.uploadedFile = fileValue.target.files[0].name;
    this.imageData = {
      contentType: fileValue.target.files[0].type,
      fileName: `dynamic-menu/${this.uploadedFile}`
    };
    this.imageInformation = this.uploadedFile;
    if (this.validateImage(fileValue.target.files)) {
      this.permissionService.uploadKey(this.imageData).subscribe(
        (res: any) => {
          this.urlPath = res.data;
          const uploadAttachment: any = document.getElementById('file');
          const uploadAttachmentDetails = uploadAttachment.files[0];
          this.httpService.putUpload(this.urlPath, uploadAttachmentDetails, uploadAttachmentDetails.type).subscribe(
            (_resp: any) => {
              this.alertService.success('Uploaded Successfully!');
              const reader = new FileReader();
              reader.onload = () => {
                const value = this.sanitizer.bypassSecurityTrustUrl(reader.result as string);
                this.pageForm.patchValue({ thumbnail: value as string });
              };
              reader.readAsDataURL(file);
              this.attachType = fileValue.target.files[0].type;
            },
            (error: any) => {
              if (error.status == 0) {
                this.alertService.error('AppConstants.errorMessage');
                this.uploadedFile = '';
                this.urlPath = '';
              }
            }
          );
        },
        (_error: any) => {
          this.alertService.error('AppConstants.errorMessage');
        }
      );
    }
  }

  validateImage(file: any) {
    const fileSize = Number(file[0].size) / 1024;
    const filetype = file[0].type.replace('image/', '');
    if (
      filetype === 'jpg' ||
      filetype === 'jpeg' ||
      filetype === 'png' ||
      filetype === 'gif' ||
      filetype === 'svg+xml'
    ) {
      if (fileSize < 50) {
        return true;
      } else {
        this.imageInformation = 'Image size exceeds 50 KB';
        this.alertService.warn('File is bigger than 50 KB');
        return false;
      }
    } else {
      this.imageInformation = '';
      this.alertService.warn(`${filetype} format is not supported`);
      return false;
    }
  }
  searchPermissionList(event: Event) {
    const value = (event.target as HTMLInputElement).value.toLowerCase();
    this.pages = this.duplicatepages.filter((a: any) =>
      a?.name?.toLowerCase().startsWith(value)
    );
  }
  onModelPermission(value: string): void {
    this.modelPermissiomName = value.replace(/\w\S*/g, (txt) => txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase());
  }
}
